import { useState } from "react";
import { useAppDispatch } from "../../../app/hooks";
import { setName } from "../controllerSlice";

const emojis = [
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''
];

export const ControllerWaitingForName = () => {
  const [name, setLocalName] = useState("");
  const d = useAppDispatch();

  const onSendNickName = () => {
    if (!name || !name.trim()) {
      return;
    }

    d(setName(name + ' ' + emojis[Math.floor(Math.random() * emojis.length)]));
  }
  return (
    <div>
      <h1>Enter your nickname</h1>
      <input value={name} onChange={(e) => setLocalName(e.target.value)} />
      <button onClick={onSendNickName}>Submit</button>
    </div>
  );
};
